import React from 'react'
import Link from 'gatsby-link'
import Github from './github-24.svg'
import Mail from './mail-24.svg'
import Twitter from './twitter-24.svg'

const Footer = ({ siteTitle }) => (
  <footer className="row">
    <div className="col-12">
      <hr className="page" />
    </div>
    <div className="col-6">
      Made with <a href="https://www.gatsbyjs.org/">GatsbyJS</a> and <a href="http://simplegrid.io/">Simple Grid</a>
    </div>
    <div className="col-6 right">
      <a href="https://github.com/UlisesAC4" rel="me"><img src={Github}></img></a>
      <a href="mailto:info@ulisesavila.com" rel="me"><img src={Mail}></img></a>
      <a href="https://twitter.com/UlisesAC4" rel="me"><img src={Twitter}></img></a>
    </div>
    
    
  </footer>
)

export default Footer



